import { Component, AfterViewInit } from '@angular/core';

import {PiDataService} from './pi-data.service';
import {ChartComponent} from './chart.component';
import {KPIComponent} from './kpi.component';


@Component({
    selector: 'home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.css']
})
export class HomeComponent implements AfterViewInit {

    title = 'Fuel Score';

    private data1: any;
    private trucks: any;
    private error: any;
    private todayProd: any = {
        title: "Power KPI",
        value: "79.5",
        units: "%"
    }

    private yesterdayProd: any = {
        title: "Utilization",
        value: "85.2",
        units: "%"
    }

    private monthlyProd: any = {
        title: "Alerts",
        value: "24",
        units: ""
    }

    constructor(
        private piDataService: PiDataService
    ){

    }

    ngOnInit(): void {
        this.piDataService.getTrucks().subscribe(
            response => {
                this.trucks = response.json().Items;
                
            }, error => {
                this.error = error.json();
            }
        );
    }

    ngAfterViewInit() {
        this.piDataService.getPIData().subscribe(
            response => {
                this.data1 = response.json();
            }, error => {
                this.error = error.json();
            }
        );

    }

}



